"use server";

import { revalidatePath } from "next/cache";
import { redirect } from "next/navigation";
import { createClient } from "@/utils/supabase/server";

export async function signup(formData: FormData) {
  console.log("=== SIGNUP ACTION START ===");

  const supabase = await createClient();

  const password = formData.get("password") as string;
  const confirmPassword = formData.get("confirmPassword") as string;
  const email = formData.get("email") as string;

  console.log("Signup attempt for email:", email);

  // Validate password match
  if (password !== confirmPassword) {
    console.log("Password mismatch");
    return { error: "Passwords do not match" };
  }

  const data = {
    email: email,
    password: password,
  };

  console.log("Calling supabase.auth.signUp...");
  const { data: authData, error } = await supabase.auth.signUp(data);

  console.log("SignUp result:", {
    user: authData.user?.id,
    session: !!authData.session,
    error: error?.message,
  });

  if (error) {
    console.log("Signup error:", error.message);
    return { error: error.message };
  }

  console.log("Signup successful, revalidating and redirecting...");
  revalidatePath("/", "layout");

  // Always redirect to payment for new signups
  console.log("Redirecting to payment");
  redirect("/payment");
}

export async function signInWithGoogle() {
  const supabase = await createClient();
  const { error } = await supabase.auth.signInWithOAuth({
    provider: "google",
    options: {
      redirectTo: `${process.env.NEXT_PUBLIC_SITE_URL}/auth/callback?next=/payment`,
    },
  });

  if (error) {
    redirect("/error?message=" + encodeURIComponent(error.message));
  }
}

